import ErmjBaseHandler from "./ErmjBaseHandler";

export default class ErmjChatHandler extends ErmjBaseHandler{
    protected execute(msg){
        let cType = msg._para.ctype;
        let fromSit = this.SitHelper.serverSToLocalN(msg._para.from_chair);
        let emoji = msg._para.emoji;
        let toSit = this.SitHelper.serverSToLocalN(msg._para.to_chair);
        let fromPlayer = this.mainUI.getPlayer(fromSit);
        let toPlayer = this.mainUI.getPlayer(toSit);
        if (!fromPlayer || !fromPlayer.someone || !toPlayer || !toPlayer.someone)   // 没人不播放
            return;
        if (cType == 0){
            // 文字 暂无
        }
        else if (cType == 1){
            // 表情
            let fWPos = fromPlayer.getPlayerHeadCenterPos();
            let tWPos = toPlayer.getPlayerHeadCenterPos();
            this.mainUI.headTipsManager.playAct(emoji, fWPos, tWPos, toSit);
        }
    }

    public checkInQueue(){
        return false;
    }
}